@charset "UTF-8";
@import "../../../style/theme/default/var.css";
.tmp-form-scroll{
  height:100px;
  /*overflow-y: scroll;*/
}
/* 配置页面样式 */
@component-namespace tmp{
 @b configure {
   padding: var(--theme-padding-middle);
   background-color: var(--color-white);

     @e content {
       margin-right: var(--theme-margin-middle);
     .el-collapse-item__header{
       height: 30px;
       line-height: 30px;
     }
       .el-collapse .el-collapse-item__header__arrow:before{
         line-height: 30px;
       }
       .el-collapse-item__content {
         padding: 0;
       }
     }
      @e list {
        background-color: var(--theme-background-color-primary);
        padding: var(--theme-padding-base);
        & .list-item{
            cursor: move;
            padding:var(--theme-padding-small);
        font-size: var(--theme-font-size-small);
        &.sortable-chosen {
           background-color: var(--theme-background-color-secondary);
        color: var(--color-white);
         }
        }
      }
     @e field {
      & .field-item{
         margin-right: var(--theme-padding-base);
         margin-bottom: var(--theme-padding-base);
         position: relative;
         height: 30px;
         line-height: 28px;
         background-color: var(--color-white);
         color: var(--theme-font-color-primary);
         border-color:var(--theme-border-color-primary);
         padding:0 var(--theme-padding-middle);
        &.el-tag{
         .el-icon-close{
           position: absolute;
           top:-8px;
           right:-8px;
           background-color: var(--theme-color-secondary);
           color:var(--color-white);
           border-radius: 100%;
           display: none;
         }
        }
        &:hover{
         &.el-tag .el-icon-close{
           display: block;
          }
         }
       }
     }
    @e title{
      @utils-clearfix;
      .control{
        cursor: pointer;
        padding:0 var(--theme-padding-base);
      }
    }
     @e form {
     }
      @e tabs{
        &.el-tabs .el-tabs__content{
        padding: 0;
      }
      }

  }
}


.el-tab-pane > .tmp-configure {
  padding: 0;
}



